@import '../../styles/colors';
@import '../../styles/fonts';

.worldmap {
  flex: 1 1 auto;
  margin: 1em 0 0;
  min-height: 1px;

  .mapframe {
    background-color: transparent;
    border: none;
    height: 100%;
    width: 100%;
  }

  .mapwrapper {
    background-position: center;
    background-size: cover;
    border-radius: 8px;
    height: 100%;
    margin: 0 auto;
    max-width: 720px;
    overflow: hidden;
    width: 100%;
  }
}

.mapwrapper {
  background-image: url('../../assets/background.png');
}

