@import '../../styles/colors';
@import '../../styles/fonts';

.seedinfo {
  color: $pale-green;

  .seed {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;

    .seednumber {
      .seednumberbox {
        user-select: all;
      }
    }

    .toggle {
      align-items: center;
      cursor: pointer;
      display: flex;
      flex-flow: row nowrap;

      &:hover {
        color: lighten($color: $pale-green, $amount: 15);
      }
    }
  }
}
